import colors from "../styles/colors";
import ListManageStudent from "./list/list-manage-student";
import CustomPagination from "./paging/pagination";
import { useCallback, useEffect, useState } from "react";
import { styled } from "styled-components";

const StyleDiv = styled.div`
    position: relative;
    width: 100%;
    height: ${({studentdatalength}) =>
        (studentdatalength === 0 ? '30vh' : 'auto')};
    padding: 4rem 2.4rem;
    background-color: ${colors.gray5};
    border-radius: 0.833vw;
`;

const MyPageStudent = ({studentData, studentButtonListener}) => {
    const [slicedData, setSlicedData] = useState([]);
    const [page, setPage] = useState(1);
    const itemsPerPage = 4; //   
    const totalPageCount = Math.ceil((studentData.length) / itemsPerPage); //   
    const pageRange = 5;

    const pageListener = (page) => {
        setPage(page);
    };

    // useEffect(() => {
    //     //     
    //     const startIndex = (page - 1) * itemsPerPage;
    //     const endIndex = startIndex + itemsPerPage;
    //     setSlicedData(studentData.slice(startIndex, endIndex));
    // }, [])

    useEffect(() => {
        //     
        const startIndex = (page - 1) * itemsPerPage;
        const endIndex = startIndex + itemsPerPage;
        setSlicedData(studentData.slice(startIndex, endIndex));
    }, [studentData, page])

    return (
        <StyleDiv studentdatalength={studentData.length}>
            <div style={{display: "flex", flexDirection: "column", gap: "1rem", paddingLeft: "8px", paddingRight: "8px"}}>
                <p style={{fontSize:"2.4rem", fontWeight:"700"}}> </p>
                {studentData.length > 0 && <p style={{fontSize:"1.5rem", color:`${colors.gray2}`}}>       .     </p>}
            </div>
            
            {/* studentData   ListManageStudent   */}
            {studentData.length === 0 && <p style={{position: 'absolute', color:'#7F85A3', fontSize: '2rem', top: '50%', left: '50%', transform: 'translate(-50%, -50%)'}}>   .</p>}
            {studentData.length > 0 && <ListManageStudent data={slicedData} studentButtonListener={studentButtonListener }/>}
            {studentData.length > 0 && <CustomPagination page={page} itemsPerPage={itemsPerPage} totalPageCount={totalPageCount} totalDataCount={studentData.length} pageListener={pageListener} />}
        </StyleDiv>
    )
}

export default MyPageStudent;