import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import { styled } from "styled-components";
import colors from "../../styles/colors";

const StyleModal = styled.div`
    width: 45.2rem;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border-radius: 0.417vw;
    padding: 2rem;

    /*   */
    z-index: 999;
    
    /*   */
    /* top, bottom, left, right    . */
    /* translate    . */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
`

const StyleButton = styled.button`
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: ${({text}) =>
        (text === '' ? colors.gray1 : '#FB5D5D')};
    color: ${({text}) =>
        (text === '' ? colors.gray6 : '#fff')};
    font-size: 2rem;
    font-weight: 700;
    border-radius: 0.417vw;
    padding: 2.2rem;
`

const StyleTitle = styled.p`
    color: ${colors.black};
    align-self: center;
    font-size: 2rem;
    font-weight: 700;
    margin-top: 3.2rem;
    margin-bottom: 4.7rem;
`

const PermissionModal = (props) => {
  return (
    <Modal
      {... props}
      centered
      backdrop="static"
      keyboard={false}
    >
      <StyleModal>
        {props.state === '' && <StyleTitle>'{props.name}'   ?</StyleTitle>}
        {props.state === '' && <StyleTitle>'{props.name}'  ?</StyleTitle>}
        <div style={{display:'flex', flexDirection:'row', gap:'1.6rem'}}>
            <StyleButton text={''} onClick={props.onHide}></StyleButton>
            {props.state === '' && <StyleButton text={''} onClick={() => {
              props.handlemodalreject(props.name, props.state);
              props.onHide();
            }}></StyleButton>}
            {props.state === '' && <StyleButton text={''} onClick={() => {
              props.handlemodalremove(props.name, props.state);
              props.onHide();
            }}></StyleButton>}
        </div>
      </StyleModal>

    </Modal>
  );
}

export default PermissionModal;